module.exports = formatLinkAsAutolink;

var toString = require('mdast-util-to-string');

function formatLinkAsAutolink(node) {
  var raw = toString(node);
  return (// If theres a url
    node.url && // And theres a no title
    !node.title && ( // And if the url is the same as the content
    raw === node.url || 'mailto:' + raw === node.url) && // And that starts w/ a protocol
    /^[a-z][a-z+.-]+:/i.test(node.url) && // And that doesnt contain ASCII control codes (character escapes and
    // references dont work) or angle brackets
    !/[\0- <>\u007F]/.test(node.url)
  );
}